#pageHeader{
  padding-left: 0;
  .navbar{
		padding: 0;
	}
	.navbar-toggler{
		color: white;
		width: 50%;
    padding: .2rem;
    background: black;
    position: absolute;
    right: 2px;
    top: 2px;
    min-height: 60px;
		text-align: right;
	}
  .navbar-brand{
		display: block;
		width: 50%;
		min-height: 60px;
		background: url('../images/logo.png') no-repeat 0px 0px;
		background-size: contain;
		border-bottom: 2px solid black;
		margin: 0;
		@include media-breakpoint-up(sm) {
			width: 310px;
			min-height: 110px;
			margin-right: 1rem;
		}
	}
	#primaryNav{
		text-align: left;
		@include media-breakpoint-up(sm) {
			align-self: flex-end;
			text-align: right;
		}
	}
	.navbar-nav{
		@include media-breakpoint-down(sm) {
			text-align: right;
			padding-right: 5px;
		}
	}
	.nav-link{
		color: white;
		text-transform: uppercase;
		text-shadow: 1px 1px 1px rgba($brand-success, 0.0);
		border-bottom: 1px solid transparent;
		margin-bottom: 3px;
		&:hover, &:focus{
			text-decoration: none;
			color: darken(white, 15%);
			border-bottom: 1px solid $brand-success;
			text-shadow: 1px 1px 1px rgba($brand-success, 0.8);
		}
		.active{
			border-bottom: 1px solid $brand-success;
		}
	}



}
@include media-breakpoint-up(sm) {
	#menu-primary{
		// margin-top: 7.4rem;
	}
}



