/*
 * all pages
 *
 */
.page-title{
	margin: 2rem 0;
}
@include media-breakpoint-up(md) {
	.page-title{
		border-bottom: 0;
		margin: 3rem 0;
	}
}
.page article{
	@extend .clearfix;
	margin: 0 0 7rem;
}
.heading-block{
	@extend .clearfix;
	background-color: rgba(black,.6);
	color: $brand-success;
}
.news-heading{
	margin: 3rem 0;
	// display: inline-block;
	font-size: 5rem;
	&:hover{
		text-decoration: none;
	}
}


/*
 * home page
 *
 */

.home .main{
	// padding-top: 5rem;
	padding-bottom: 5rem;
	.btn-twitter-nojs{
		max-width: 400px;
	}
}

@include media-breakpoint-up(md) {
	.mc4wp-form{
		padding: 16px 26px;
	}
	.sidebar{
		max-width: 320px;
		// margin-right: 3rem;
		float: right;
	}
}
/*
 * about page
 *
 */
/**
 * leadership page
 * @uses user template file
 */
.user-profiles{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.user-profile{
		text-align: center;
		padding-bottom: 1rem;
		flex: 1 1 auto;
    padding-bottom: 1rem;
    min-width: 180px;
	}
	h3{
		font-size: 18px;
		font-weight: bold;
		color: #555;
		margin: .5rem 0 0;
	}
	.img-thumbnail{
		border-radius: 50%;
		max-width: 120px;
		height: auto;
	}
	p{
		margin: 0;
		font-size: .95rem;
		a{
			font-weight: 300;
		}
	}
	.modal-title .small{
		color: #555;
	}
}




